import quickBtn from '@/components/mbbo/quickBtn/quickBtn.vue';
import homeSearch from '@/components/mbbo/homeSearch/homeSearch.vue';
export default {
	data() {
		return {
			search: this.Static + 'mbcImg/common/search.png',
			searchLS: '',
			arr: [],
			list:[]
		};
	},
	methods: {
		//触发搜索按钮
		searchSS() {
			if (this.searchLS == '') {
				uni.showToast({
					title: '请输入商机名称',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else {
				this.list.unshift(this.searchLS);
				this.arr=[...new Set(this.list)];
				console.log(this.arr);
				if(this.arr.length>10){
					this.arr.splice(10,1);
					this.arr.length=10;
				}
				console.log(this.arr)
				var _this=this;
				uni.setStorage({
					key: 'storage_search',
					data: _this.arr,
					success: function() {
						console.log('success');
					}
				});
				this.searchLS='';
				this.request();
			}
		},
		searLS(e){
			console.log(e)
		},
		request(){
			let params = {
				page:'1',
				userId:'760',
				name:this.searchLS,
			}; // 请求总数居时 参数为空
			uni.showLoading({ // 展示loading
				title: '加载中'
			});
			uni.request({
				url: this.api2 + '/rest-rp/adve/findList', //接口地址。
				// data: this.endParams(params),
				method: 'POST',
				// header: {
				// 	Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
				// },
				success: (response) => {
					uni.hideLoading();
					console.log(response.data);
					
				},
				fail: (error) => {
					uni.hideLoading(); // 隐藏 loading
					uni.showToast({
						title: '网络繁忙，请稍后',
						icon: 'none',
						duration: 1000
					});
					console.log(error, '网络繁忙，请稍后');
				}
			});
		},
		fist(){
			var _this=this
			uni.getStorage({
				key: 'storage_search',
				success: function (res) {
					_this.list=[...new Set(res.data)];
					_this.arr=_this.list;
				}
			});
		},
		deleadd(){
			this.arr=[];
			this.list=[];
			var _this=this;
			uni.setStorage({
				key: 'storage_search',
				data: _this.arr,
				success: function() {
					console.log('success');
				}
			});
			console.log(this.arr);
		}
	},
	created() {
		this.fist();
	},
	components: {
		quickBtn,
		homeSearch
	}
};
